.nweet {
  margin-bottom: 20px;
  background-color: white;
  width: 100%;
  max-width: 320px;
  padding: 20px;
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.8);
}

.container {
  width: 100%;
  max-width: 320px;
  height: 80px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.edit {
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 5px;
}

.input {
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid black;
  text-align: center;
  background-color: white;
  color: black;
}

.btn {
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: #04aaff;
  cursor: pointer;
}

.cancel {
  cursor: pointer;
  background-color: tomato;
}

.nweet img {
  right: -10px;
  top: 20px;
  position: absolute;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin-top: 10px;
}

.nweet h4 {
  font-size: 14px;
}

.actions {
  position: absolute;
  right: 10px;
  top: 10px;
}

.actions span {
  cursor: pointer;
}

.actions span:first-child {
  margin-right: 10px;
}